:root {
  --minH: 40px;
  @media (min-width: 768px) {
    --minH: 80px;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
}

.list [data-board='bun']:first-child div {
  border-radius: 88px 88px 40px 40px;
  max-width: 100%;
}
.list [data-board='bun']:last-child div {
  border-radius: 40px 40px 88px 88px;
  max-width: 100%;
}


@media (max-width: 520px) {
  .list [class="constructor-element"] {
    padding: 10px 8px;
    padding-right: 20px;
    font-size: 1rem;
    display: flex;
    justify-content: space-between;
  }

  .list [class="constructor-element"] > span {
    width: 100%;
  }

  .list [class="constructor-element"] img {
    width: 55px;
    margin-right: 0;
  }
  .list [class="constructor-element"] svg {
    display: none;
  }

  .list [class="constructor-element__action pr-2"] {
    display: none;
  }
}

.inner {
  width: 100%;
  min-height: var(--minH);
  max-height: 350px;
  padding: 10px 0;
  overflow: hidden;
}

.inner > div {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 10px;
  height: 100%;
  max-height: 350px;
  overflow: auto;
}

.inner > div > div {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  flex-grow: 1;
  gap: 10px;
}
.inner > div > div > div {
  display: flex;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  max-width: 100%;
  gap: 10px;
}

.inner > div > div > div > div {
  max-width: 100%;
}

div[data-board='bun'] {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
}

.footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  padding: 0 20px;
}

.footer button {
  /* margin-left: auto; */

  font-family: 'JetBrains Mono';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  /* box-shadow: 0px 0px 16px 8px rgba(51, 51, 255, 0.25),
    0px 0px 8px 8px rgba(51, 51, 255, 0.25); */
}



@media (min-width: 768px) {
  .footer {
    margin: 40px 0;
  }
  .footer button {
    margin-left: 40px;
    padding: 20px 40px;
  }
}

.price {
  font-family: 'Iceland';
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 36px;
}

.price span {
  margin-right: 10px;
}

.empty {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  padding: 16px 24px;

  border-radius: 20px;
  background: #2f2f37;

  width: 100%;
  min-height: var(--minH);

  font-family: 'Jet Brains Mono';
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  color: var(--text-inactive-color);
  flex-grow: 1;
  box-sizing: border-box;
}

.emptyBunTop {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 24px;
  min-height: var(--minH);
  font-family: 'Jet Brains Mono';
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  color: var(--text-inactive-color);
  flex-grow: 1;
  box-sizing: border-box;
  background: #2f2f37;
  border-radius: 88px 88px 40px 40px;
}
.emptyBunBottom {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  min-height: var(--minH);
  padding: 16px 24px;
  font-family: 'Jet Brains Mono';
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  color: var(--text-inactive-color);
  flex-grow: 1;
  box-sizing: border-box;
  background: #2f2f37;
  border-radius: 40px 40px 88px 88px;
}

.border {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 2px solid transparent;
  border-radius: 20px;
}

.border > div > div {
  max-width: 100%;
}


@media (max-width: 520px) {
  .footer {
    margin: 1rem 0;
    padding: 0;
    justify-content: space-between;
  }

  .footer button {
    padding: 10px 12px;
  }

}
