.main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  min-height: 88vh;
}

.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
  max-width: 480px;
  width: 100%;
}

.inner {
  margin-bottom: 1rem;
}

.inner,
.inner > div {
  width: 100%;
}

.inner [class*="input_size_default"] {
  width: 100%;
}

.link {
  text-decoration: none;
  color: rgba(76, 76, 255, 1);
}

.button_type_primary {
  padding: 20px 40px;
  border: none;
  cursor: pointer;
  outline: none;
  border-radius: var(--common-border-radius);
  transition: var(--common-transition);
  color: var(--text-primary-color);
  background: linear-gradient(63.18deg, #801ab3 0%, #4c4cff 100%);
}

.button_type_primary:hover {
  box-shadow: 0px 0px 16px 8px rgba(51, 51, 255, 0.25),
    0px 0px 8px 8px rgba(51, 51, 255, 0.25);
  filter: drop-shadow(0px 4px 32px rgba(51, 51, 255, 0.5));
}

.button_type_primary:active {
  opacity: 0.9;
}

input[type=email]:not(:placeholder-shown):invalid {
  color: red;
  outline-color: red;
}
